import React, {useRef} from "react";
import '../App.css';
import axios from "axios";
import moment from 'moment';
import 'moment/locale/ru';
import sendIcon from '../images/send-button.svg'
import pusher from "../pusher";
import {NavLink, useParams} from "react-router-dom";

function Chat(){
    const {user_id, object_id} = useParams();

    const [messages, setMessages] = React.useState([]);
    const [loading, setLoading] = React.useState(true);
    const [messageText, setMessageText] = React.useState("");
    const chatRef = useRef(null);

    React.useEffect(() => {
        async function getMessages() {
            const response = await axios.get("https://www.agents-bot.mrflip.ru/chats/get_chat_history/"+ object_id +"/" + user_id + "/");

            setMessages(response.data);

            chatRef.current.scrollTop = chatRef.current.scrollHeight;
        }
        getMessages();

        const channel = pusher.subscribe('property-' + object_id);
        channel.bind('message', socketMessage => {
            // console.log('Received event:', socketMessage.text);

            if(socketMessage.from_user_id != user_id){
                // console.log('Received event:', socketMessage.text);

                const currentDate = new Date().toISOString();

                console.log(messages)

                setMessages(prevMessages => [...prevMessages, {
                    "created_at":currentDate,
                    "from_user_id": -1,
                    "id":0,
                    "is_read":true,
                    "property_id":object_id,
                    "text":socketMessage.text,
                    "to_user_id":socketMessage.from_user_id
                }])
            }

        });

        return () => {
            pusher.unsubscribe('property-' + object_id);
        };
    }, []);

    React.useEffect(() => {
        chatRef.current.scrollTop = chatRef.current.scrollHeight;
    }, [messages]);

    async function sendMessageText(e){
        e.preventDefault();

        const currentDate = new Date().toISOString();
        setMessages([...messages, {
            "created_at":currentDate,
            "from_user_id": user_id,
            "id":0,
            "is_read":false,
            "property_id":object_id,
            "text":messageText,
            "to_user_id":-1
        }])

        const data = {
            "text": messageText,
            "channel": "property-" + object_id,
            "from_user_id": user_id,
            "to_user_id": -1
        }
        setMessageText("");

        const response = await axios.post("https://www.agents-bot.mrflip.ru/chats/send_message/", data);
    }

    if(!messages) return(
        "????????????????..."
    )

    return (
        <>
            <div className="chat-header">
                <NavLink to={"/" + user_id} className="prev-page">
                    <svg width="12" height="22" viewBox="0 0 12 22" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M3.60206 11.1885L11.4062 3.23933C11.9866 2.64818 11.9778 1.69847 11.3867 1.1181C10.7955 0.537729 9.84583 0.546471 9.26546 1.13762L0.429623 10.1376C-0.143208 10.7211 -0.143208 11.6559 0.429623 12.2393L9.26546 21.2393C9.84583 21.8305 10.7955 21.8392 11.3867 21.2589C11.9778 20.6785 11.9866 19.7288 11.4062 19.1376L3.60206 11.1885Z" fill="white"/>
                    </svg>
                    {/*<span>??????????</span>*/}
                </NavLink>
                <div className="chat-header-name">
                    {/*<img src="home-icon.png" alt="" />*/}
                    ?????????????????? ???????????? 3??1
                    {/*<img src="home-icon.png" alt="" />*/}
                </div>
                <div></div>
            </div>

            <div className="chat-body" id="chatBody" ref={chatRef}>
                {messages.map((message) =>
                    <>
                        {message.from_user_id === -1 ?
                            <div className="chat-body-item">
                            <span className="chat-body-item-text">
                                {message.text}
                                <span className="chat-body-item-datetime">{moment(message.created_at).format("MMMM d, HH:mm")}</span>
                            </span>
                            </div>
                            :
                            <div className="chat-body-item right">
                            <span className="chat-body-item-text">
                                {message.text}
                                <span className="chat-body-item-datetime">{moment(message.created_at).format("MMMM d, HH:mm")}</span>
                            </span>
                            </div>
                        }
                    </>
                )}
            </div>
            <form onSubmit={sendMessageText} className="chat-footer">
                <input type="text" value={messageText} onChange={(e) => setMessageText(e.target.value)}/>
                <button><img src={sendIcon} alt="send-button-icon"/></button>
            </form>
        </>
    );
}

export default Chat;
