import React from "react";
import axios from "axios";
import pusher from "../pusher";
import {useParams, NavLink} from "react-router-dom";
import homeIcon from "../images/home-icon.png";

function Index(){
    const {user_id} = useParams();
    const [objects, setObjects] = React.useState([]);

    React.useEffect(() => {
        async function getObjects() {
            const response = await axios.post("https://www.agents-bot.mrflip.ru/api/user/"+ user_id +"/get_properties/");
            console.log(response.data)
            setObjects(response.data);
        }
        getObjects();
    }, []);

    return (
        <>  
            <script src="https://telegram.org/js/telegram-web-app.js">
            </script>
            <script>
                window.Telegram.WebApp.expand()
            </script>
            <div className="chat-list-header">
                <span></span>
            </div>
            <div className="chat-list">
                {objects.map((object) =>
                    <NavLink to={"/chat/"+ user_id +"/" + object.id} className="chat-list-item">
                        <div className="avatar">
                            <img src={homeIcon} alt="" />
                        </div>
                        <span className="name">{object.id}) {object.address}</span>
                    </NavLink>
                )}
            </div>
        </>
    );
}

export default Index;
